import { _decorator, CharacterController, Component, Node, v3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('CatController')
export class CatController extends Component {
    start() {

    }

    update(deltaTime: number) {
        const movement = v3(0.1, 0.1, 0);
        let characterController = this.node.getComponent(CharacterController);
        characterController?.move(movement);
    }
}
